.PokemonList {
  width: 95%;
  margin: 0 auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.Pokemon {
  padding: 0.5rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-evenly;
  transition: 250ms;
  cursor: pointer;
  box-shadow: 3px 3px 8px #383838, inset 6px 6px 12px rgba(0, 0, 0, 0.2),
    inset -10px -10px 20px rgba(0, 0, 0, 0.3);
}

.Pokemon img {
  width: 8rem;
  height: 8rem;
  border: 2px solid #096860;
  border-radius: 50%;
  transform: scale(0.9);
  padding: 0.25rem;
  background-color: #d7fdfb;
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.5),
    inset -2px -2px 4px rgba(255, 255, 255, 0.5);
  transition: 250ms;
}

.Pokemon:hover {
  transform: scale(1.01) translateY(-0.25rem);
}

.Pokemon:hover.Pokemon img {
  background-color: #b4e9e6;
}

.Type {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.25rem;
  color: #096860;
}
