.SearchForm {
  max-width: 40em;
  padding: 1rem;
  margin: 1rem auto 2rem;
  display: flex;
  gap: 0.25rem;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
  box-shadow: 3px 3px 8px #383838, inset 6px 6px 12px rgba(0, 0, 0, 0.2),
    inset -10px -10px 20px rgba(0, 0, 0, 0.3);
}

.SearchForm div {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.SearchForm input {
  padding: 0.25rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
  max-width: 9.5rem;
  outline: none;
  transition: 250ms;
  position: relative;
}

.SearchForm input:hover {
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.5);
}

.SearchForm button {
  padding: 0.3rem 1rem;
  border: none;
  border-radius: 0.25rem;
  background-color: #2ca399;
  color: #fff;
  cursor: pointer;
  transition: 250ms;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.SearchForm button:hover {
  background-color: #078873;
}

@media (max-width: 40em) {
  .SearchForm {
    flex-direction: column;
    gap: 0.5rem;
  }

  .SearchForm button {
    width: 100%;
    max-width: 19rem;
  }
}
